video {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
}

.frame__title{
    font-family: fantasy,sans-serif;
}
span{
    margin-top: 5px;
}
a{
    margin-top:10px;
}

.copyright{
    text-align: center;
    margin-top: 30px;
    font-family: rucksack, sans-serif;
    font-size: 12px;
}

a {
	text-decoration: none !important;
	color: white;
	position: relative;
}
 a:hover{
     background-color: white;
 }
     .slideshow{
    padding-top: 450px;
}

.frame {
	text-align: center;
	position: relative;
	z-index: 100;
	color: whitesmoke;
    padding: 10px !important;
    margin-left: 7px;
}

.frame a {
	color: white;
}

.frame__links {
	display: inline;
}

.slide {
	margin: 0;
}

.slide,
.slide__img-wrap,
.slide__img {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

.slide {
	display: flex;
	align-items: center;
	justify-content: center;
}

.slide__img-wrap {
	overflow: hidden;
}

.slide__caption {
	position: relative;
	padding: 0 10vw;
	width: 100%;
}

.slides__caption-headline {
	font-size: 6.5vw;
	font-size: clamp(2rem,6.5vw,6rem);
	line-height: 0.9;
	margin: 0;
	font-weight: normal;
}

.text-row {
	position: relative;
	overflow: hidden;
	display: block;
	white-space: nowrap;
}

.text-row > span {
	display: block;
	position: relative;
	padding: 0.3rem 0;
}
#slant{
    font-style: oblique 50deg;
}
.slides__caption-headline {
	font-weight: 300;
}

.slides__caption-link {
	display: inline-block;
	margin-top: 1rem;
	text-indent: 0.2vw;
	font-size: 1.75rem;
	font-weight: 300;
	font-size: clamp(1rem, 5vw, 1.75rem);
    border: 1px white solid;
    padding: 10px 15px;
}

@media screen and (min-width: 53em) {
   
	.frame {
		position: fixed;
		text-align: left;
		z-index: 100;
		top: 0;
		left: 0;
		display: grid;
		align-content: space-between;
		width: 100%;
		height: 100vh;
		padding: 2rem 3.5rem;
		grid-template-columns: 30% 40% 30%;
    }
	.frame__title {
		margin: 0;
		grid-area: title;
	}

    .copyright{
        text-align: left !important;
        font-size: 25px;
    }
}
