.rotatewarning { display: none; }
    @media only screen and (orientation:portrait){
        .thrillervideo { display:none; }
        .explorebtn{display: none;}
        .rotatewarning { display:block; }

    }
    @media only screen and (orientation:landscape){
        .rotatewarning { display:none; }
        .thrillervideo { display:block; }
        .explorebtn{display: block;}
    }

    ::-webkit-scrollbar {
        width: 0;
        background: transparent;
    }

    iframe{
        width: 100vw;
        height: 100vh;
        margin: 0;
        padding: 0;
    }

    .thrillervideo{
        margin: 0;
        padding: 0; 
    }
    .explorebtn{
        font-size: 25px;
        padding:5px;
        background: rgba(0, 0, 0, 1);
        margin-bottom: 7px;
    }

    button {
        display: inline-block;
        border-radius: 4px !important;
        background-color: #f4511e;
        border: none;
        color: #FFFFFF;
        text-align: center;
        padding-bottom: 5px;
        width: 200px;
        transition: all 0.5s;
        margin: 10px;
      }
      
      button span {
        cursor: pointer;
        display: inline-block;
        position: relative;
        transition: 0.5s;
      }
      
      button span:after {
        content: '\00bb';
        position: absolute;
        opacity: 0;
        top: 0;
        right: -20px;
        transition: 0.5s;
      }
      
      button:hover span {
        padding-right: 25px;
      }
      
      button:hover span:after {
        opacity: 1;
        right: 0;
      }
      button{
          text-align: center;
      }